import React, { Component, Fragment } from 'react';
import IframeComm from 'react-iframe-comm';
import { Grid, LinearProgress } from '@material-ui/core';
import { connect } from 'react-redux';
import axios from 'axios';

class Terminal extends Component {
    state = {
        uid: localStorage.getItem("nemu-uid"),
        isBlocklyLoaded: false
    }

    webconsolePath = 'http://45.64.99.168/webconsole/';

    onReady = () => {

        this.setState({
            isBlocklyLoaded: true
        });

        const { uid } = this.state;

        const link = this.webconsolePath;
        // const link = (this.props.rxs.domain.isDevelopmentMode) ?
        //     this.webconsolePath :
        //     `http://localhost:80/nemu-dev/nemu/public/webconsole/` :
        //     `${window.location.origin}/webconsole/`;

        const config = {
            headers: {
                'Content-Type': 'multipart/form-data'
            },
            proxy: { port: 80 }
        };

        // soon, change the hostname in data.webConsole to username of user
        const command = [
            ("IF exist " + uid + "/nul ( echo " + uid + " exists ) ELSE (mkdir " + uid + ")"),
            ("g++ main.cpp -o main"),
            (this.props.rxs.domain.isDevelopmentMode ? "main" : "./main")
        ];
        const data = {
            webConsole1: {
                "jsonrpc": "2.0",
                "method": "run",
                "params": ["NO_LOGIN", { "user": "", "hostname": "arrokh", "path": "" }, command[0]]
            },
            webConsole2: {
                "jsonrpc": "2.0",
                "method": "run",
                "params": ["NO_LOGIN", { "user": "", "hostname": "arrokh", "path": "" }, command[1]]
            },
            webConsole3: {
                "jsonrpc": "2.0",
                "method": "run",
                "params": ["NO_LOGIN", { "user": "", "hostname": "arrokh", "path": "" }, command[2]],
                "id": uid
            },
            rwAccess: { "uid": uid, "code": this.props.code }
        }
        axios
            .post(link + "webconsole.php", data.webConsole1, config)
            .then(res => {
                return axios.post(link + "rw-access/index.php", data.rwAccess, config);
            }, err => this.onAxiosRejected(err, "In phase 1 : "))
            .then(res => {
                return axios.post(link + "webconsole.php?uid=" + uid, data.webConsole2, config);
            }, err => this.onAxiosRejected(err, "In phase 2 : "))
            .then(res => {
                return axios.post(link + "webconsole.php?uid=" + uid, data.webConsole3, config);
            }, err => this.onAxiosRejected(err, "In phase 3 : "))
            .then(res => {
                this.setState({
                    output: res.data.result.output,
                    messages: "Successfully compiled your code :)",
                    isLoading: false
                });
                this.handleGetOutput(res.data.result.output);
            }, err => this.onAxiosRejected(err, "In phase 4 : "))
            .catch(err => {
                console.log("CompileButton::handleCompileBtn->" + err);
            });
    }

    render() {
        const { uid } = this.state;
        // const height = this.props.custHeight ? this.props.custHeight : "72.1vh";
        const heightDiv = this.props.custHeightRoot ? this.props.custHeightRoot : "70vh";
        const attributes = {
            src: this.webconsolePath + "/webconsole.php?uid=" + uid,
            // src: window.location.origin + "/webconsole/webconsole.php?uid=" + uid,
            width: "100%",
            height: "100%"
        }

        /*
        const renderData = !(this.props.rxs.domain.isDevelopmentMode) ? (
            <Grid
                style={{ height: height }}
                container
                direction="row"
                justify="center"
                alignItems="center"
            >
                <Typography component="p" style={{ color: 'white' }}>
                    Oops, it doesn't work now. Try again later.
                </Typography>
            </Grid>
        ) : (
                <Fragment>
                    {
                        !this.state.isBlocklyLoaded ? (
                            <Grid container spacing={0} justify={"center"} alignItems="center" style={{
                                height: "100%",
                                position: "fixed",
                                zIndex: 3
                            }}>
                                <LinearProgress />
                            </Grid>
                        ) : null
                    }
                    <IframeComm
                        attributes={attributes}
                        handleReady={this.onReady}
                    />
                </Fragment>
            );
        */

        return (
            <div style={{ backgroundColor: 'black', height: heightDiv }}>
                {/* {renderData} */}
                <Fragment>
                    {
                        !this.state.isBlocklyLoaded ? (
                            <Grid container spacing={0} justify={"center"} alignItems="center" style={{
                                height: "100%",
                                position: "fixed",
                                zIndex: 3
                            }}>
                                <LinearProgress />
                            </Grid>
                        ) : null
                    }
                    <IframeComm
                        attributes={attributes}
                        handleReady={this.onReady}
                    />
                </Fragment>
            </div>
        );
    }
}

const mapStateToProps = (state) => {
    return {
        rxs: {
            domain: state.domain,
        }
    }
}

export default connect(mapStateToProps)(Terminal);