import React, { Fragment, useState, useEffect } from 'react';
import { Button, Icon, Dialog, DialogTitle, DialogContent, DialogActions, DialogContentText, Grid, Fab, Tooltip } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import axios from 'axios';
import { connect } from 'react-redux'
import ReactGA from "react-ga";
import { useEnv } from '../../../../helper/hooks/envHook';


const CompileButtonHook = (props) => {

    const { classes, fullScreen, showAsButton, code, getOutput } = props;
    const { domain } = useEnv();

    const [open, setOpen] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [output, setOutput] = useState('');
    const [error, setError] = useState(null);

    const [axiosCancelToken, setAxiosCancelToken] = useState(axios.CancelToken.source());

    const handleCompileBtn = (e) => {
        ReactGA.event({
            category: 'Miscellaneous tool',
            action: 'Compile code by button'
        });

        if (!domain) {
            console.error("Domain is null")
            return;
        }

        setOpen(true);
        setIsLoading(true);

        const config = {
            headers: {
                'Content-Type': 'application/json'
            },
            cancelToken: axiosCancelToken.token
        };

        const data = {
            language: "cpp",
            script: code,
        };
        axios.post(`${domain}/execute/jdoodle`, data, config)
            .then(res => {
                setOutput(res.data.output);
                handleGetOutput(output);
            })
            .catch(err => {
                // setMessages('Something went wrong :(');
                setError(err);
            })
            .finally(() => {
                setIsLoading(false);
            });
    }

    const handleGetOutput = (output) => {
        if (getOutput) getOutput(output);
    }

    const handleAbort = () => {
        setOpen(false);
        axiosCancelToken.cancel();
        setAxiosCancelToken(axios.CancelToken.source());
    }

    const handleClose = () => {
        setOpen(false);
        // setError(false);
    };
    useEffect(() => {
        console.log(error);
        return () => {
        };
    }, [error]);

    const renderCompileCode = (isLoading) ? (
        <DialogContentText>Compiling...</DialogContentText>
    ) : (
            <div>
                <pre>
                    <code className="language-cpp">
                        {/* {messages === "" ? output : messages} */}
                        {output}
                    </code>
                </pre>
            </div>
        );

    const renderButtonOption = (isLoading) ? (
        <Button style={{ justifyContent: 'center' }} onClick={handleAbort} variant="contained" color="secondary">
            Abort
            </Button>
    ) : (
            <React.Fragment>
                {/* {!error && messages === '' ? (
                    <Button style={{ justifyContent: 'center' }} onClick={handleShow} color="primary" variant="outlined" >
                        Show
                    </Button>
                ) : null} */}
                <Button style={{ justifyContent: 'center', marginLeft: 15 }} onClick={handleClose} color="primary">
                    Close
                </Button>
            </React.Fragment>
        );

    return (
        <Fragment>
            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
                disableBackdropClick={(isLoading) ? true : false}
                disableEscapeKeyDown={(isLoading) ? true : false}
            >
                <DialogTitle id="responsive-dialog-title">{"Terminal Output:"}</DialogTitle>
                <DialogContent>
                    <hr />
                    {renderCompileCode}
                    <hr />
                </DialogContent>
                <DialogActions>
                    <Grid
                        container
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        {renderButtonOption}

                    </Grid>
                </DialogActions>
            </Dialog>
            {
                showAsButton ? (
                    <>
                        <Button onClick={handleCompileBtn} variant="contained" color="default" fullWidth >
                            Compile
                            <Icon className={classes.rightIcon}>gavel</Icon>
                        </Button>
                    </>
                ) : (
                        <Tooltip title="Run you code!" placement="top">
                            <Fab onClick={handleCompileBtn} size="medium" color="secondary" aria-label="Add" className={classes.fabButton}>
                                <Icon>gavel</Icon>
                            </Fab>
                        </Tooltip>
                    )
            }

        </Fragment>
    );
}

const styles = theme => ({
    rightIcon: {
        marginLeft: theme.spacing.unit,
    },
    fabButton: {
        position: 'absolute',
        zIndex: 1,
        top: -40,
        left: 0,
        right: 0,
        margin: '0 auto',
    }
});

const mapStateToProps = (state) => {
    return {
        rxs: {
            domain: state.domain,
        }
    }
}

export default withStyles(styles)(connect(mapStateToProps)(CompileButtonHook));
