import { useState, useEffect, useCallback } from "react";

export const useEnv = () => {

    const [domain, setDomain] = useState(null);


    const getDomain = useCallback(
        () => {
            const port = 8080;
            const hostLocation = window.location.host.split(":")[0];
            if (hostLocation === "localhost")
                setDomain(`http://${hostLocation}:${port}`);
            else
                setDomain(`https://${hostLocation}`);
        }, []
    );

    useEffect(() => {
        getDomain();
        return () => {

        };
    }, [getDomain]);
    return {
        domain
    };
};