import React, { Component } from 'react';
import { connect } from 'react-redux';
import { toast } from 'react-toastify';
import { Icon, Button, Grid } from '@material-ui/core';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { withStyles } from '@material-ui/core/styles';
import { playgroundCodeUpdate } from '../../../../store/project/projectAction';
import Recaptcha from "react-recaptcha";
import axios from 'axios';
import ReactGA from "react-ga";

class ShareButton extends Component {
    state = {
        buttonClick: false,
        toastId: null,
        isToastOpen: false,
        openDialog: false,
        isVerified: false,
        toEmail: "",
        fromEmail: "",
        messages: "",
    }

    handleClick = () => {
        if (toast.isActive("sync-button"))
            return;

        this.setState({ isToastOpen: true });

        toast("Synchronizing...", {
            toastId: "sync-button",
            autoClose: 1500,
            onClose: () => { this.setState({ isToastOpen: false }) }
        })

        this.props.rxd.playgroundCodeUpdate({
            code: this.props.code,
            xmlCode: "empty"
        });
    }

    onClickHandler = () => {
        ReactGA.event({
            category: 'Miscellaneous tool',
            action: 'Share code by submited email'
        });

        this.setState({ openDialog: true })
    }

    closeDialogHandler = () => {
        this.setState({
            openDialog: false,
            buttonClick: false,
            isVerified: false,
        });
    }

    recaptchaHandler = () => {
        this.setState({ isVerified: true });
    }

    submitHandler = () => {

        this.setState({ isToastOpen: true });

        toast("Sending...", {
            toastId: "sync-button",
            autoClose: 10000,
            onClose: () => { this.setState({ isToastOpen: false }) }
        })

        this.setState({ buttonClick: true });
        const dataPost = {
            fromEmail: this.state.fromEmail,
            toEmail: this.state.toEmail,
            messages: this.state.messages,
            code: this.props.code
        }


        const link = (this.props.rxs.domain.isDevelopmentMode) ?
            `http://localhost:80/nemu-dev/nemu/public/share/index.php` :
            `${window.location.origin}/share/index.php`;

        const config = {
            headers: {
                'Content-Type': ['application/json']
            }
        };

        axios.post(link, dataPost, config)
            .then(res => {
                this.setState({
                    openDialog: false,
                    buttonClick: false,
                });

                toast("Done :)", {
                    toastId: "sync-button",
                    autoClose: 2000,
                    onClose: () => { this.setState({ isToastOpen: false }) }
                })
            });
    }

    verifiedComp = () => {
        if (this.state.isVerified) {
            return (
                <Button
                    variant={'contained'}
                    onClick={this.submitHandler}
                    color="primary"
                    disabled={(this.state.fromEmail.length < 1 || this.state.toEmail.length < 1 || this.state.messages.length < 1) || this.state.buttonClick}
                >
                    Submit
                </Button>
            );
        }
    }

    inputChangeHandle = (e) => {
        this.setState({
            [e.target.id]: e.target.value
        });
    }

    render() {
        const { classes } = this.props;

        return (
            <React.Fragment>
                <Button
                    onClick={this.onClickHandler}
                    variant="contained"
                    color="default"
                    fullWidth
                >
                    Share
                    <Icon className={classes.rightIcon}>share</Icon>
                </Button>
                <Dialog
                    open={this.state.openDialog}
                    onClose={this.closeDialogHandler}
                    aria-labelledby="form-dialog-title"
                    disableBackdropClick
                    disableEscapeKeyDown
                >
                    <DialogTitle id="form-dialog-title" align="center">Let's share your project</DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            NEMU ID will help you send your project throught email to anyone. Try it!
                        </DialogContentText>
                        <br />
                        <TextField
                            autoFocus
                            margin="dense"
                            id="fromEmail"
                            label="Your email address"
                            type="fromEmail"
                            required
                            fullWidth
                            onChange={this.inputChangeHandle}
                        />
                        <TextField
                            margin="dense"
                            id="toEmail"
                            label="To"
                            type="toEmail"
                            required
                            fullWidth
                            onChange={this.inputChangeHandle}
                        />
                        <TextField
                            margin="dense"
                            id="messages"
                            label="Messages"
                            type="text"
                            multiline={true}
                            required
                            fullWidth
                            onChange={this.inputChangeHandle}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Grid item xs={12}>
                            <Grid container alignItems={'center'} style={{ textAlign: 'center' }} >
                                <Grid item xs>
                                    <Button onClick={this.closeDialogHandler} color="primary">
                                        Cancel
                                    </Button>
                                </Grid>
                                <Grid item xs>
                                    <Recaptcha
                                        sitekey="6LfsCqMUAAAAAMkS7ioQaTFV8AQ-H_Yd15nWihtt"
                                        render="explicit"
                                        verifyCallback={this.recaptchaHandler}
                                        hidden={this.state.isVerified}
                                    />
                                </Grid>
                                <Grid item xs>
                                    {this.verifiedComp()}
                                </Grid>
                            </Grid>
                        </Grid>
                    </DialogActions>
                </Dialog>
            </React.Fragment>
        );
    }
}

const styles = theme => ({
    rightIcon: {
        marginLeft: theme.spacing.unit,
    }
});

const mapStateToProps = (state) => {
    return {
        rxs: {
            domain: state.domain,
            uid: state.firebase.auth.uid
        }
    }
}
const mapDispatchToProps = (dispatch) => {
    return {
        rxd: {
            playgroundCodeUpdate: data => dispatch(playgroundCodeUpdate(data))
        }
    }
}

export default withStyles(styles)(connect(mapStateToProps, mapDispatchToProps)(ShareButton));