import React, { Fragment, useState } from 'react';
import { Grid } from '@material-ui/core';
import styles from './styles';
import MiscellaneousTool from '../../components/core-nemu/misc-tools/MiscellaneousTool';
import Blockly from '../../components/core-nemu/visual-code/Blockly';
import ToolsButton from '../../components/core-nemu/misc-tools/ToolsButton';

import { Steps } from 'intro.js-react';
import 'intro.js/introjs.css';
import Typography from "@material-ui/core/Typography";
import withWidth, { isWidthUp } from '@material-ui/core/withWidth';
import { withStyles } from '@material-ui/core/styles';

const Playground = (props) => {
    const [code, setCode] = useState("");

    const onVisualSpaceReady = () => {
        console.log("ASD");
    }

    const handleGetCode = code => {
        setCode(code);
    }


    let isNewUser = localStorage.getItem("nemu-new-playground");
    let showSteps = true;
    if (isNewUser === null || isNewUser === undefined) {
        localStorage.setItem("nemu-new-playground", "true");
    } else {
        showSteps = false;
    }

    const [stepsEnabled, setStepsEnabled] = useState(showSteps);
    const initialStep = 0;
    const steps = [
        {
            element: '.welcome-1',
            intro: 'Susun Algoritma mu secara visual disini',
        },
        {
            element: '.welcome-2',
            intro: 'Perhatikan hasil konversi kode program dan kompilasi kode secara online disini',
        },
        {
            element: '.welcome-3',
            intro: 'Melalui tombol-tombol dibawah ini, kamu bisa membagikannya ke teman mu, meng-unduh kode program mu, atau melakukan kompilasi online',
        },
    ];

    const onExit = () => {
        setStepsEnabled(false);
    };


    return (
        <Fragment>
            <Steps
                enabled={stepsEnabled}
                steps={steps}
                initialStep={initialStep}
                onExit={onExit}
            />

            <Grid container spacing={0}>
                <Grid className="welcome-1" item xs={12} md={7} style={{ height: "86.8vh" }}>
                    <Blockly getCode={handleGetCode} onReady={onVisualSpaceReady} />
                </Grid>
                <Grid item xs={12} md={5} style={{ height: "81vh" }}>
                    <Grid container justify={"center"}
                        alignItems={"center"} alignContent={"center"}>
                        <Grid className="welcome-2" item xs={12} style={{ height: "100%" }}>
                            <MiscellaneousTool code={code} />
                        </Grid>
                        <Grid className="welcome-3" item xs={11} style={{ left: -10 }}>
                            <ToolsButton code={code} />
                        </Grid>
                    </Grid>
                </Grid>
            </Grid>
            <Grid container style={{
                backgroundColor: "#3f51b5",
                display: ' flex',
                minHeight: ' 7vh',
                flexDirection: ' column'
            }}>
                <Grid item xs={12}>
                    {
                        isWidthUp('md', props.width) ?
                            <Typography variant="caption" align={"center"} style={{ paddingTop: 10, paddingBottom: 10 }}>
                                <a style={{ color: 'white', textDecoration: 'none' }} target="_blank"
                                    rel="noopener noreferrer" href="https://arrokhs.wordpress.com/about-me/">
                                    Arrokh ¬© 2018-2019. All right reserved.
                                </a>
                            </Typography>
                            : null
                    }
                </Grid>
            </Grid>
        </Fragment>
    );
}

export default withWidth()(withStyles(styles)(Playground));