import React, {Fragment, useState} from 'react';
import IframeComm from 'react-iframe-comm';
import {CircularProgress, Typography} from "@material-ui/core";
import Grid from "@material-ui/core/Grid";

const Blockly = (props) => {
    const [isBlocklyLoaded, setIsBlocklyLoaded] = useState(false);

    const {custHeight} = props;

    const attributes = {
        src: window.location.origin + "/blockly/blockly.html",
        width: "100%",
        height: custHeight ? custHeight : "100%"
    };

    const onReceiveMessage = (e) => {
        if (e.data.code)
            props.getCode(e.data.code);
    };

    const onReady = () => {
        setIsBlocklyLoaded(true);
    };

    return (
        <Fragment>
            {
                !isBlocklyLoaded ? (
                    <Grid container direction={"column"} spacing={0} justify={"center"} alignItems="center" style={{
                        height: "100%",
                        background: "rgba(29, 39, 49, 0.9)",
                        position: "fixed",
                        zIndex: 3
                    }}>
                        <CircularProgress thickness={8} size={75} style={{marginRight: 20}}/>
                        <Typography variant={"display1"} style={{color: 'white'}}>
                            <br/>
                            <span role="img" aria-label="Man Technologist">üë®‚Äçüíª</span> Preparing The Environment <span role="img" aria-label="Woman Technologist">üë©‚Äçüíª</span>
                        </Typography>
                    </Grid>
                ) : null
            }
            <IframeComm
                attributes={attributes}
                handleReady={onReady}
                handleReceiveMessage={onReceiveMessage}
            />
        </Fragment>
    );
}

export default Blockly;