import React, {useEffect, useState} from 'react';
import Grid from '@material-ui/core/Grid';
import {withStyles} from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import {Icon} from '@material-ui/core';
import Terminal from './Terminal';
import CodeOutput from './CodeOutput'
import ReactGA from "react-ga";

const MiscellaneousTool = (props) => {
    const [value, setValue] = useState(0);

    const handleChange = (e, v) => {
        setValue(v);

        ReactGA.event({
            category: 'Miscellaneous tool',
            action: 'Opening ' + (v === 0 ? 'Code' : 'Console') + ' tab'
        });
    };

    const {classes, code, openInCourse} = props;
    let custData = {
        heightCodeOutput: null,
        heightTerminalDiv: null,
        styleRoot: {
            height: ''
        }
    }

    if (openInCourse) {
        custData.heightCodeOutput = "100vh";
        custData.heightTerminalDiv = "73vh";
        custData.styleRoot.height = "72vh";
    }

    useEffect(() => {
        if (value === 1)
            handleChange(null, 0); // eslint-disable-next-line
    }, [code]);

    return (
        <div className={classes.root} style={custData.styleRoot}>
            <Grid container className={classes.container} justify={"center"}
                  alignItems={"center"} alignContent={"center"}>
                <Grid item xs={11}>
                    <AppBar position="static">
                        <Tabs value={value} onChange={handleChange} centered>
                            <Tab icon={<Icon>code</Icon>}/>
                            <Tab icon={<Icon>dvr</Icon>}/>
                        </Tabs>
                    </AppBar>
                    {value === 0 && <CodeOutput code={code} custHeight={custData.heightCodeOutput}/>}
                    {value === 1 && <Terminal code={code} custHeightRoot={custData.heightTerminalDiv}
                                              custHeight={custData.heightCodeOutput}/>}
                </Grid>
            </Grid>
        </div>
    )
}

const styles = theme => ({
    root: {
        flexGrow: 1
    },
    container: {
        height: '100%'
    },
    rightIcon: {
        marginLeft: theme.spacing.unit,
    }
});

export default withStyles(styles)(MiscellaneousTool);