import React, { Component } from 'react';
import { Icon, Button } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import ReactGA from "react-ga";

class DownloadButton extends Component {

    handleClick = () => {
        ReactGA.event({
            category: 'Miscellaneous tool',
            action: 'Download code'
        });

        const { code } = this.props;

        const file = new Blob([code], { type: 'text/plain' });
        const link = document.createElement('a');

        link.href = URL.createObjectURL(file);
        document.body.appendChild(link);
        link.download = "main.cpp"
        link.click();
        document.body.removeChild(link);
    }

    render() {

        const { classes } = this.props;

        return (
            <React.Fragment>
                <Button onClick={this.handleClick} variant="contained" color="default" fullWidth >
                    Download
                    <Icon className={classes.rightIcon}>save_alt</Icon>
                </Button>
            </React.Fragment>
        );
    }
}

const styles = theme => ({
    rightIcon: {
        marginLeft: theme.spacing.unit,
    }
});

export default withStyles(styles)(DownloadButton);