export const playgroundCodeUpdate = (data) => {
    return (dispatch, getState, { getFirebase, getFirestore }) => {
        const uid = getState().firebase.auth.uid;

        const firestore = getFirestore();

        firestore
            .collection("users")
            .doc(uid)
            .update({playground: data})
            .then(res => {
            })
            .catch(err => {
                // console.log(err);
            });
    };
}